.page1bg {
	width: 100vw;
	height: 100vh;
	display: flex;
	justify-content: center;
	align-items: center;
	z-index: 1000;
}
.Page1Content {
	width: 50vw;
	height: 60vh;

	background: rgba(255, 255, 255, 0.1);
	/* background: linear-gradient(180.31deg, #500a85 -6.88%, #240467 110.16%); */
	opacity: 0.9;
	box-shadow: 3px 1px 38px -12px #000000;
	border-radius: 28px;
	display: flex;
	justify-content: center;
	align-items: center;
	z-index: 1000;
}
.left,
.right {
	width: 50%;
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
}
.equinoxlogo {
	width: 80%;
	/* height: 245px; */
	filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}
.dates {
	font-family: Roboto;
	font-style: normal;
	font-weight: 500;
	font-size: 2vw;
	line-height: 19px;
	text-align: right;
	margin-top: 0;
	color: #ffffff;
}
.timer {
	font-family: Roboto;
	font-style: normal;
	font-weight: 500;
	font-size: 4vw;
	line-height: 42px;
	text-align: right;
	color: #ffffff;
	margin-top: 0;
}

#devfolio-apply-now {
	-ms-flex-align: center;
	-webkit-box-align: center;
	align-items: center;
	outline: none;
	background-color: #3770ff;
	border-radius: 3px;
	border: none;
	color: #ffffff;
	cursor: pointer;
	display: -ms-flexbox;
	display: -webkit-box;
	display: flex;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Helvetica, Arial,
		sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol';
	font-size: 18px;
	font-weight: 600;
	padding: 12px 20px;
	text-align: center;
	text-transform: lowercase;
	cursor: pointer;
}

#devfolio-apply-now:not([disabled]) {
	cursor: pointer;
}

#devfolio-apply-now:hover {
	background-color: #2954bf !important;
}
